<template>
    <section id="introBlock" class="row">
        <h2 class="text-start text-md-center text-break">Contact gegevens</h2>
    </section>
    <section id="contactInfo" class="row">
        <div class="row mb-3">
            <div class="col-sm-3">
                <label for="voornaam" class="col-form-label">Voornaam</label>
                <input type="text" class="form-control " id="voornaam" v-model="formData.voornaam"
                    @blur="v$.formData.voornaam.$touch">
                <small v-if="v$.formData.voornaam.$error" class="text-danger">Dit is een verplicht veld.</small>
            </div>
            <div class="col-sm-3">
                <label for="tussenvoegsel" class="col-form-label">Tussenvoegsel</label>
                <input type="text" class="form-control" id="tussenvoegsel" v-model="formData.tussenvoegsel">
            </div>
            <div class="col-sm-6">
                <label for="achternaam" class="col-form-label">Achternaam</label>
                <input type="text" class="form-control" id="achternaam" v-model="formData.achternaam"
                    @blur="v$.formData.achternaam.$touch">
                <small v-if="v$.formData.achternaam.$error" class="text-danger">Dit is een verplicht veld.</small>

            </div>
        </div>

        <div class="row mb-3">
            <div class="col-sm-7">
                <label for="email" class="col-form-label">E-mailadres</label>
                <input type="email" class="form-control " id="email" v-model="formData.email"
                    @blur="v$.formData.email.$touch">
                <small v-if="v$.formData.email.$error" class="text-danger">Vul een geldig e-mailadres in.</small>
            </div>
            <div class="col-sm-5">
                <label for="telefoon" class="col-form-label">Telefoonnummer</label>
                <input type="tel" class="form-control " id="telefoon" v-model="formData.telefoon"
                    @blur="v$.formData.telefoon.$touch">
                <small v-if="v$.formData.telefoon.$error" class="text-danger">Vul een geldig telefoonnummer in.</small>
            </div>


        </div>
        <div class="row mb-3">
            <div class="col-sm-3">
                <label for="geboortedatum" class="col-form-label">Geboortedatum</label>
                <input type="date" class="form-control" id="geboortedatum" v-model="formData.geboortedatum"
                    @blur="v$.formData.geboortedatum.$touch">
                <small v-if="v$.formData.geboortedatum.$error" class="text-danger">Dit is een verplicht veld.</small>
            </div>
            <div class="col-sm-9">
                <label for="" class="col-form-label">Ik ben </label>
                <div class="pt-1">
                    <div class="form-check form-check-inline  ">
                        <input class="form-check-input" type="radio" name="geslacht" id="jongen" value="jongen"
                            v-model="formData.geslacht" @blur="v$.formData.geslacht.$touch">
                        <label class="form-check-label" for="geslacht">
                            Jongen
                        </label>
                    </div>
                    <div class="form-check  form-check-inline ">
                        <input class="form-check-input" type="radio" name="geslacht" id="meisje" value="meisje"
                            v-model="formData.geslacht" @blur="v$.formData.geslacht.$touch">
                        <label class="form-check-label" for="geslacht">
                            Meisje
                        </label>
                    </div>
                    <div class="form-check form-check-inline ">
                        <input class="form-check-input" type="radio" name="geslacht" id="anders" value="anders"
                            v-model="formData.geslacht" @blur="v$.formData.geslacht.$touch">
                        <label class="form-check-label" for="geslacht">
                            Neutraal
                        </label>
                    </div>
                </div>
                <small v-if="v$.formData.geslacht.$error" class="text-danger">Dit is een verplicht veld.</small>
            </div>
        </div>

        <div class="row mb-3">
            <div class="col-sm-12">
                <label for="club" class="col-form-label">Bij welke club speel je?</label>
                <input type="text" class="form-control " id="club" v-model="formData.club"  @blur="checkClubfield">
                <small v-if="v$.formData.club.$error" class="text-danger">Dit is een verplicht veld.</small>
            </div>
        </div>
    </section>


    <section id="buttonBlock" class="d-flex justify-content-between ">
            <button class="btn btn-lg btn-light border "  @mouseup="back()">&lt;&lt; Terug </button>
            <button class="btn btn-lg btn-primary "  @mouseup="submit()">Verder &gt;&gt;</button>
    </section>

</template>

<script>
import { useVuelidate } from '@vuelidate/core'
import { required, email, numeric} from '@vuelidate/validators'

export default {
    name: "Step3",
    props: {
        contactInfo: Object,
    },
    data() {
        return {
            formData: {
                voornaam: '',
                tussenvoegsel: '',
                achternaam: '',
                geslacht: '',
                geboortedatum: '',
                email: '',
                telefoon: '',
                club: '',
            },
            v$: useVuelidate(),
        }
    },
    validations() {
        return {
            formData: {
                voornaam: { required },
                achternaam: { required },
                geslacht: { required },
                geboortedatum: { required },
                email: { required, email },
                telefoon: { numeric, required },
                club: { required },
            }
        }
    },
    methods: {

        submit() {
            this.v$.$validate()
            if (this.v$.$error) {
                return
            }
            this.$emit('next', { key: 'contact', value: this.formData });
        },
        back() {
            this.$emit('back');
        }
    },
    mounted() {
        if (this.contactInfo !== undefined) {
            this.formData = this.contactInfo;
        }
    }
}
</script>

