<template>
    <section id="introBlock" class="row">
        <h2 class="text-start text-md-center text-break">Bepaal je regio</h2>
        <p class="text-start text-md-center text-break">Aan de hand van je thuis- en schooladres wordt je regio
            bepaald.</p>
    </section>
    <section id="thuisSchoolInfoBlock" class="row">
        <div id="thuisInfo" class="col-sm-12 col-md-6 ">
            <h3 class="text-start text-md-center text-break">Thuis adres</h3>
            <div class="row mb-3">
                <label for="straat-thuis" class="col-sm-4 col-form-label">Straat + huisnummer</label>
                <div class="col-sm-8">
                    <input type="text" class="form-control" id="straat-thuis" v-model="formData.thuis.straat"
                        @blur="v$.formData.thuis.straat.$touch">
                    <small v-if="v$.formData.thuis.straat.$error" class="text-danger">Dit is een verplicht veld.</small>
                </div>
            </div>

            <div class="row mb-3">
                <label for="postcode-thuis" class="col-sm-4 col-form-label">Postcode</label>
                <div class="col-sm-8">
                    <input type="text" class="form-control" id="postcode-thuis" v-model="formData.thuis.postcode"
                        @blur="v$.formData.thuis.postcode.$touch, checkForRegion('thuis')">
                    <small v-if="v$.formData.thuis.postcode.$error" class="text-danger">Dit is een verplicht
                        veld.</small>
                    <small v-if="errorThuis" class="text-danger">Vul een geldige postcode in.</small>
                </div>
            </div>

            <div class="row mb-3">
                <label for="plaats-thuis" class="col-sm-4 col-form-label">Plaats</label>
                <div class="col-sm-8">
                    <input type="text" class="form-control" id="plaats-thuis" v-model="formData.thuis.plaats"
                        @blur="v$.formData.thuis.plaats.$touch">
                    <small v-if="v$.formData.thuis.plaats.$error" class="text-danger">Dit is een verplicht veld.</small>
                </div>
            </div>
        </div>
        <div id="schoolInfo" class="col-sm-12 col-md-6 ">
            <h3 class="text-start text-md-center text-break">Schooladres </h3>
            <div class="row mb-3">
                <label for="naam-school" class="col-sm-4 col-form-label">Naam school</label>
                <div class="col-sm-8">
                    <input type="text" class="form-control" id="naam-school" v-model="formData.school.naam"
                        @blur="v$.formData.school.naam.$touch">
                    <small v-if="v$.formData.school.naam.$error" class="text-danger">Dit is een verplicht veld.</small>
                </div>
            </div>
            <div class="row mb-3">
                <label for="straat-school" class="col-sm-4 col-form-label">Straat + huisnummer</label>
                <div class="col-sm-8">
                    <input type="text" class="form-control" id="straat-school" v-model="formData.school.straat"
                        @blur="v$.formData.school.straat.$touch">
                    <small v-if="v$.formData.school.straat.$error" class="text-danger">Dit is een verplicht
                        veld.</small>

                </div>
            </div>
            <div class="row mb-3">
                <label for="postcode-school" class="col-sm-4 col-form-label">Postcode</label>
                <div class="col-sm-8">
                    <input type="text" class="form-control" id="postcode-school" v-model="formData.school.postcode"
                        @blur="v$.formData.school.postcode.$touch, checkForRegion('school')">
                    <small v-if="v$.formData.school.postcode.$error" class="text-danger">Dit is een verplicht
                        veld.</small>
                    <small v-if="errorSchool" class="text-danger">Vul een geldige postcode in.</small>
                </div>
            </div>
            <div class="row mb-3">
                <label for="plaats-school" class="col-sm-4 col-form-label">Plaats</label>
                <div class="col-sm-8">
                    <input type="text" class="form-control" id="plaats-school" v-model="formData.school.plaats"
                        @blur="v$.formData.school.plaats.$touch">
                    <small v-if="v$.formData.school.plaats.$error" class="text-danger">Dit is een verplicht
                        veld.</small>
                </div>
            </div>
        </div>
    </section>

    <section v-if="formData.thuis.regio != '' && formData.school.regio != ''" id="regioResultBlock" class="row  mt-2 ">
        <h3 id="regioResult" class="text-start text-md-center">
            {{ regioResultTxt }}
        </h3>
        <div id="preferRegion" v-if="formData.thuis.regio != formData.school.regio" class="text-start text-md-center ">
            <p>Je mag voor twee regio's uitkomen. Kies je hier onder een voorkeursregio.</p>
            <div class="form-check form-check-inline">
                <input class="form-check-input" type="radio" name="regioPrefered" id="home"
                    :value="formData.thuis.regio" v-model="formData.regioVoorkeur" checked>
                <label class="form-check-label" for="home">{{ formData.thuis.regio }}</label>
            </div>
            <div class="form-check form-check-inline">
                <input class="form-check-input" type="radio" name="regioPrefered" id="school"
                    :value="formData.school.regio" v-model="formData.regioVoorkeur">
                <label class="form-check-label" for="school">{{ formData.school.regio }}</label>
            </div>
        </div>
    </section>

    <section id="buttonBlock" class="d-flex justify-content-between ">
        <button class="btn btn-lg btn-light border  " @mouseup="back()">&lt;&lt; Terug </button>
        <button class="btn btn-lg btn-primary " @mouseup="submit()">Verder &gt;&gt;</button>
    </section>

</template>

<script>
import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'


export default {
    name: "Step2",
    props: {
        regioInfo: Object,
    },
    data() {
        return {
            formData: {
                thuis: {
                    straat: "",
                    postcode: "",
                    plaats: "",
                    regio: "",
                },
                school: {
                    naam: "",
                    straat: "",
                    postcode: "",
                    plaats: "",
                    regio: "",
                },
                regioVoorkeur: "",
            },
            v$: useVuelidate(),
            errorSchool: false,
            errorThuis: false,
            regioResultTxt: "",
        }

    },
    validations() {
        return {
            formData: {
                thuis: {
                    straat: { required },
                    postcode: { required },
                    plaats: { required },
                },
                school: {
                    naam: { required },
                    straat: { required },
                    postcode: { required },
                    plaats: { required },
                }
            }

        };
    },
    methods: {
        checkForRegion(type) {
            // url's voor API(1):*********/
            const url_api_coordinaten1 = 'https://services.arcgis.com/nSZVuSZjHpEZZbRo/ArcGIS/rest/services/Postcodepunten/FeatureServer/0/query?where=postcode6+%3D++%27';
            const url_api_coordinaten2 = '%27&objectIds=&time=&geometry=&geometryType=esriGeometryEnvelope&inSR=&spatialRel=esriSpatialRelIntersects&resultType=none&distance=0.0&units=esriSRUnit_Meter&returnGeodetic=false&outFields=postcode6&returnGeometry=true&featureEncoding=esriDefault&multipatchOption=xyFootprint&maxAllowableOffset=&geometryPrecision=&outSR=28992&datumTransformation=&applyVCSProjection=false&returnIdsOnly=false&returnUniqueIdsOnly=false&returnCountOnly=false&returnExtentOnly=false&returnQueryGeometry=false&returnDistinctValues=false&cacheHint=false&orderByFields=&groupByFieldsForStatistics=&outStatistics=&having=&resultOffset=&resultRecordCount=&returnZ=false&returnM=false&returnExceededLimitFeatures=true&quantizationParameters=&sqlFormat=none&f=pjson';
            // url's voor API(2):*********/
            let requestUrlCoordinaten
            switch (type) {
                case 'thuis':
                    requestUrlCoordinaten = url_api_coordinaten1 + this.formData.thuis.postcode.replace(/ /g, '') + url_api_coordinaten2;
                    break;
                case 'school':
                    requestUrlCoordinaten = url_api_coordinaten1 + this.formData.school.postcode.replace(/ /g, '') + url_api_coordinaten2;
                    break;
            }
            this.createRequest(requestUrlCoordinaten, this.succeedCoordinates, type);

        },
        createRequest(url, succeed, type) {
            fetch(url)
                .then((response) => this.fail_api(response))
                .then((data) => succeed(data, type))
                .catch((error) => this.fail(error, type));
        },
        fail_api(response) {
            if (!response.ok) {
                throw Error(response.statusText);
            }
            return response.json();
        },
        succeedCoordinates(data, type) {
            this.getRegion(data.features[0].geometry.x, data.features[0].geometry.y, type);
        },
        getRegion(x, y, type) {
            const url_api_regio1 = 'https://services.arcgis.com/emS4w7iyWEQiulAb/ArcGIS/rest/services/KNBSB_Leaguegrenzen/FeatureServer/0/query?where=1%3D1&objectIds=&time=&geometry=';
            const url_api_regio2 = '&geometryType=esriGeometryPoint&inSR=&spatialRel=esriSpatialRelIntersects&resultType=none&distance=0.0&units=esriSRUnit_Meter&returnGeodetic=false&outFields=League&returnGeometry=false&returnCentroid=false&featureEncoding=esriDefault&multipatchOption=xyFootprint&maxAllowableOffset=&geometryPrecision=&outSR=&datumTransformation=&applyVCSProjection=false&returnIdsOnly=false&returnUniqueIdsOnly=false&returnCountOnly=false&returnExtentOnly=false&returnQueryGeometry=false&returnDistinctValues=false&cacheHint=false&orderByFields=&groupByFieldsForStatistics=&outStatistics=&having=&resultOffset=&resultRecordCount=&returnZ=false&returnM=false&returnExceededLimitFeatures=true&quantizationParameters=&sqlFormat=none&f=pjson&token=';
            const requestUrlRegio = url_api_regio1 + x + ',' + y + '%2C' + url_api_regio2;
            this.createRequest(requestUrlRegio, this.success_api_regio, type, this.fail);
        },
        success_api_regio(data, type) {
            switch (type) {
                case "thuis":
                    this.errorThuis = false;
                    this.formData.thuis.regio = data.features[0].attributes.League;
                    break;
                case "school":
                    this.errorSchool = false;
                    this.formData.school.regio = data.features[0].attributes.League;
                    break;
            }
            this.showRegios();

        },

        fail(error, type) {
            console.log(error);
            switch (type) {
                case "thuis":
                    this.errorThuis = true;
                case "school":
                    this.errorSchool = true
                    break;
            }
        },
        submit() {
            this.v$.$validate()
            if (this.v$.$error) {
                return
            }
            if (this.formData.school.regio !== "" && this.formData.thuis.regio !== "") {
                this.$emit('next', { key: 'regio', value: this.formData });
            }
        },
        back() {
            this.$emit('back',);
        },
        showRegios() {
            if (this.formData.thuis.regio == this.formData.school.regio) {
                this.regioResultTxt = `Je mag uitkomen voor de regio:  ${this.formData.thuis.regio}`;
           } else {
                this.regioResultTxt = `Je mag uitkomen voor de regio's: ${this.formData.thuis.regio} en ${this.formData.school.regio}`;
            }
            //  Standaard wordt de voorkeursregio automatisch de thuisregio is handige bij het versturen van de data later
            this.formData.regioVoorkeur = this.formData.thuis.regio;
        },
    },
    mounted() {
        if (typeof this.regioInfo !== "undefined") {
            this.formData = this.regioInfo;
        }
        this.showRegios();
    }
};

</script>

