<template>
    <div class="m-4">
        <Step1 v-if="activeStep === 0" @next="next" :league-info="sendData.league" />
        <Step2 v-if="activeStep === 1" @next="next" @back="back" :regio-info="sendData.regio" />
        <Step3 v-if="activeStep === 2" @next="next" @back="back" :contact-info="sendData.contact" />
        <Step4 v-if="(activeStep === 3)" @submitData="submitData" @back="back" />
    </div>
</template>

<script>
import Step1 from "@/components/forms/step1.vue";
import Step2 from "@/components/forms/step2.vue";
import Step3 from "@/components/forms/step3.vue";
import Step4 from "@/components/forms/step4.vue";

export default {
    components: {
        Step1, Step2, Step3, Step4
    },
    data() {
        return {
            activeStep: 0,
            sendData: {
                date: '',
            }
        }
    },
    methods: {
        next(payload) {
            if (this.activeStep < 3) {
                this.sendData[payload.key] = payload.value;
                this.activeStep = this.activeStep + 1;
            }
        },
        back() {
            if (this.activeStep > 0) {
                this.activeStep--
            }
        },
        async submitData() {
            // sendToZapier.sendData(this.sendData);
            this.sendData.date = `${this.$yfa.script.date(3).full}@${this.$yfa.script.time().full}`;
            //  first send general data);
            const responseMain = await this.$yfa.script.zap(JSON.stringify(this.sendData));

            let regioZapCode = ''
            // then send regio specific data
            switch (this.sendData.regio.regioVoorkeur) {
                case "Amsterdam":
                    regioZapCode = 'ocbk227';
                    break;
                case "Brabant / Zeeland":
                    regioZapCode = 'bnqqxs7';
                    break;
                case "Midden Nederland":
                    regioZapCode = 'bne7eqn';
                    break;
                case "Den Haag":
                    regioZapCode = 'bnepcxh';
                    break;
                case "Haarlem":
                    regioZapCode = 'bnelq1r';
                    break;
                case "Noord Nederland":
                    regioZapCode = 'bnex7yu';
                    break;
                case "Oost Nederland":
                    regioZapCode = 'bne0qti';
                    break;
                case "Rotterdam":
                    regioZapCode = 'bne7bbd';
                    break;
                case "Zuid Nederland":
                    regioZapCode = 'bnenioa';
                    break;
                case "Noord-Holland":
                    regioZapCode = 'bnevloa';
                    break;
                default:
                    console.error('error');
            }
            const responseRegio = await this.$yfa.script.zap(JSON.stringify(this.sendData), regioZapCode);

            if (responseRegio.status === 'success' && responseMain.status === 'success') {
                this.$router.push('/Outro');
            } else {
                console.log('error');
            }
        }
    }
}
</script>

