<template>

    <section id="introBlock" class="row">
        <h2 class="text-start text-md-center text-break">Bepaal je leeftijd en league.</h2>
        <p class="text-start text-md-center text-break">Aan de hand van je leeftijd en welke sport je doet, wordt je
            bepaald of je mag deelnemen aan het NK Little league 2023.</p>
    </section>
    <section id="leagueFieldsBlock" class="row">
        <div id="month" class="col-md-4 col-sm-12 mb-2">
            <label for="monthDropdown" class="form-label">
                <h5 class="text-break">Wat is je geboortemaand</h5>
            </label>
            <select id="monthDropdown" class="form-select" name="geboortemaand" v-model="formData.month"
                @change="calculateLeagueAge()">
                <option v-for="month in months" :value="month">
                    {{ month.name }}</option>
            </select>
            <small v-if="v$.formData.month.$error" class="text-danger">Dit is een verplicht veld.</small>
        </div>

        <div id="year" class="col-md-4 col-sm-12 mb-2">
            <label for="yearDropdown" class="form-label">
                <h5 class="text-break">Wat is je geboortejaar</h5>
            </label>
            <select id="yearDropdown" class="form-select" name="geboortejaar" v-model="formData.year"
                @change="calculateLeagueAge()">
                <option v-for="year in years" :value="year">
                    {{ year.name }}</option>
            </select>
            <small v-if="v$.formData.year.$error" class="text-danger">Dit is een verplicht veld.</small>
        </div>

        <div class="col-md-4 col-sm-12">
            <label id="sport" for="sportCodeDropdown" class="form-label">
                <h5 class="text-break">Welke sport doe je?</h5>
            </label>
            <select id="sportCodeDropdown" class="form-select" name="sport" v-model="formData.sport"
                @change="calculateLeagueAge()">
                <option v-for="sport in sports" :value="sport">
                    {{ sport.name }}</option>
            </select>
            <small v-if="v$.formData.sport.$error" class="text-danger">Dit is een verplicht veld.</small>
        </div>
    </section>

    <section id="leagueResultBlock" class="row mt-2">
        <participate class="text-start text-md-center text-break " v-if="leagueResultTxt == 0"
            :league-age="this.formData.llage" :league-play="this.formData.league" :sport="this.formData.sport.name" />
        <toold class="text-start text-md-center text-break " v-if="leagueResultTxt == 2"
            :sport="this.formData.sport.name" />
        <toyoung class="text-start text-md-center text-break " v-if="leagueResultTxt == 1"
            :league-age="this.formData.llage" :sport="this.formData.sport.name" />
    </section>
    <section id="buttonBlock" class="row gx-4  text-center" v-if="leagueResultTxt == 0">
        <div class="col-12">
            <button class="btn btn-lg btn-primary w-100" @mouseup="submit()">Verder &gt;</button>
        </div>
    </section>
</template>

<script lang="js">
import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'
import ageData from '@/assets/data/data.js';
import participate from '@/components/forms/ageresults/participate.vue';
import toold from '@/components/forms/ageresults/toold.vue';
import toyoung from '@/components/forms/ageresults/toyoung.vue';

export default {
    name: "Step1",
    components: {
        participate,
        toold,
        toyoung
    },
    props: {
        leagueInfo: {
            type: Object,
        }
    },
    data() {
        return {
            months: [
                { id: 1, name: "januari" },
                { id: 2, name: "februari" },
                { id: 3, name: "maart" },
                { id: 4, name: "april" },
                { id: 5, name: "mei" },
                { id: 6, name: "juni" },
                { id: 7, name: "juli" },
                { id: 8, name: "augustus" },
                { id: 9, name: "september" },
                { id: 10, name: "oktober" },
                { id: 11, name: "november" },
                { id: 12, name: "december" },
            ],
            years: [
                { id: 2016, name: "2016" },
                { id: 2015, name: "2015" },
                { id: 2014, name: "2014" },
                { id: 2013, name: "2013" },
                { id: 2012, name: "2012" },
                { id: 2011, name: "2011" },
                { id: 2010, name: "2010" },
                { id: 2009, name: "2009" },
                { id: 2008, name: "2008" },
                { id: 2007, name: "2007" },
                { id: 2006, name: "2006" },
                { id: 2005, name: "2005" },
                { id: 2004, name: "2004" },
                { id: 2003, name: "2003" },
            ],
            sports: [
                { id: 1, name: "Baseball" },
                { id: 2, name: "Softball" },
            ],
            formData: {
                month: {},
                year: {},
                sport: {},
                league: "",
                llage: "",
            },
            v$: useVuelidate(),
            leagueResultTxt: 99,
        }
    },
    validations() {
        return {
            formData: {
                month: { required },
                year: { required },
                sport: { required },
            }
        }
    },
    methods: {
        calculateLeagueAge() {
            if (Object.entries(this.formData.month).length !== 0 && Object.entries(this.formData.year).length !== 0 && Object.entries(this.formData.sport).length !== 0) {
                let grepResult = ageData.charterSportLeagueAgeMatrixRows.filter((e, i) => {
                    return e.SportCode == parseInt(this.formData.sport.id) &&
                        e.Year == parseInt(this.formData.year.id) &&
                        e.Month == parseInt(this.formData.month.id);
                });

                let leagueAge = grepResult.length > 0 ? grepResult[0].LeagueAge : null;
                let leaguePlay = "nvt";
                let sportCode = parseInt(this.formData.sport.id)

                switch (leagueAge) {
                    case 10:
                        leaguePlay = "Little league";
                        break;
                    case 11:
                        if (sportCode == 2) {
                            leaguePlay = "Little league";
                        } else {
                            leaguePlay = "Little of Intermediate league";
                        }
                        break;
                    case 12:
                        if (sportCode == 2) {
                            leaguePlay = "Little of Junior league";
                        } else {
                            leaguePlay = "Little, Intermediate of Junior league";
                        }
                        break;
                    case 13:
                        if (sportCode == 2) {
                            leaguePlay = "Junior of Senior league";
                        } else {
                            leaguePlay = "Intermediate, Junior of Senior league";
                        }
                        break;
                    case 14:
                        leaguePlay = "Junior of Senior league";
                        break;
                    case 15:
                        leaguePlay = "Senior league";
                        break;
                    case 16:
                        leaguePlay = "Senior league";
                        break;
                    default:
                        leaguePlay = "Training, geen NK";
                        break;
                }
                console.log(leagueAge, leaguePlay, sportCode);

                this.showleageResult(leagueAge);
                this.formData.llage = leagueAge;
                this.formData.league = leaguePlay;
              
              
            }
        },
        showleageResult(age) {
            if (age !== null && age > 9) {
                this.leagueResultTxt = 0;
            } else if (age !== null && age < 10) {
                this.leagueResultTxt = 1;
            } else {
                this.leagueResultTxt = 2;
            }
            setTimeout(() =>{
                    document.querySelector('#buttonBlock').scrollIntoView({block: "end"});
                },10)
           

        },
        submit() {
            // stop here if form is invalid
            this.v$.$validate()
            if (this.v$.$error) {
                return
            }
            this.$emit('next', { key: 'league', value: this.formData });
        }
    },
    mounted() {
        if (typeof this.leagueInfo !== "undefined") {
            this.formData = this.leagueInfo;
            this.showleageResult(this.formData.llage);
        }
    },
}
</script>
